.section__contact {
    position: relative;
    z-index: 1;
    .title__section {
      margin-bottom: 0;
    }
    .container__indication {
      position:absolute;
      border-radius: 10px 0 10px 0;
      left:2px;
      font-size: .8rem;
      box-shadow: 0 0 4px 0.5px white;
      bottom: 10px;
      color: black;
      background-color: #da4545;
      writing-mode:vertical-lr;
      padding: 10px 0;
  }
    .container__title-alert {
      text-align: center;
      margin-bottom: 50px;
      p {
        font-style: italic;
      }
    }
    .img__decorative-contact {
      display: flex;
      position: absolute;
      z-index: -1;
      opacity: .4;
      height: 90%;
      right:5rem;
    }
    .container__form {
      display: flex;
      justify-content: center;
      width: 520px;
      color: $main-color-dark-mode;
      margin-bottom: 100px;
      .formular__contact {
        font-weight: bold;
        .button__submit{
          padding: 10px;
          border-radius: 10px;
          background-color: $yellow;
          letter-spacing: 4px;
          font-size: 15px;
          transition: all 0.7s;
          margin-top: 15px;
          font-weight: 600;
          width: 100px;
        }
        .button__submit:hover {
          background-color: $yellow;
          box-shadow: 0 0 0 2px white;
        }
        .button__submit:active {
          background-color: yellow;
          color: $main-color-dark-mode;
        }
        textarea {
          display: block;
          width: 410px;
          height: 150px;
          margin-top: 10px;
          padding: 10px;
          border-radius: 6px;
        }
        .row {
          display: flex;
          justify-content: space-between;
          .column {
            display: flex;
            flex-direction: column;
            input {
              height: 30px;
              margin: 10px 10px 20px 0;
              padding: 5px;
              border-radius: 6px;
            }
            input[type="text"] {
              width: 200px;
            }
            input[type="email"] {
              width: 410px;
            }
          }
        }
      }
    }
    @media (min-width:1200px) {
      margin-left: 210px;
      .img__decorative-contact {
        width: 40%;
        right:16rem;
        height: 100%;
      }
    }
    @media(max-width: 767.98px) {
      overflow: hidden;
      .img__decorative-contact {
        width:130%;
        left:1rem
      }
    }
  
    @media(max-width:575.98px) {
      .container__form {
        max-width: 280px;
        .formular__contact {
          textarea {
            width: 270px;
          }
          .row {
            flex-direction: column;
            .column {
              input[type="email"], input[type="text"] {
                width: auto
              }
            }
          }
        }
      }
  
    }
  }